@media screen and (max-width: 992px) {
  .container {
    flex-direction: column !important;
    margin: calc(1rem + 2vw);
    padding-inline: calc(1rem + 2vw);
  }
  .editor,
  .viewer {
    width: 100% !important;
    padding-bottom: calc(2rem + 3vw) !important;
  }
  .header{
    flex-direction: column;
  }
  .title{
    margin: unset;
    padding-left: unset !important;
  }
}

main {
  /* height: 100vh; */
  width: 100%;
}

.header{
  display: flex;
  /* width: 100%; */
  justify-content: center;
  align-items: center;
}

.App-logo{
  height: 75px;
}

.title {
  font-size: 24px;
  font-weight: 100;
  text-align: center;
  padding-left: 1rem;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.heading {
  font-size: calc(1rem + 1vw);
  font-weight: 200;
  text-align: center;
  width: 100%;
  display: inline-block;
  /* padding-bottom: 10px; */
}

.editor,
.viewer {
  width: 40%;
  /* padding-bottom: calc(1rem + 1vw); */
  /* margin: calc(1rem + 1vw); */
}

.text {
  width: 100%;
  height: 70vh;
  overflow-y: auto;
  /* padding: 10px !important; */
  padding: calc(0.5rem + 1vw);
}

.render,
.empty-viewer {
  border: 1px solid gray;
  width: 100%;
  /* min-width: 100%; */
  height: 70vh;
  overflow-y: auto;
  /* user-select: all; */
  /* padding: 10px !important; */
  padding: calc(0.5rem + 1vw);
}

.empty-viewer {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: 100;
  user-select: none !important;
  flex-direction: column;
}


